import {Injectable} from '@angular/core';
import {JwtHelper} from 'angular2-jwt';
//import { JwtHelperService } from '@auth0/angular-jwt';


//const jwtHelper = new JwtHelperService();
const jwtHelper = new JwtHelper();

/**
 * A Service to manage the JWT
 */
@Injectable({
  providedIn: 'root'
})
export class JwtService {

  static SESSION_STORAGE_KEYS(): any {
    return {
      TOKEN: 'auth-token',
      ERCODE: 'erCode',
      BREADCRUMB_STATES: 'breadcrumb-states'
    };
  }

  constructor() {

  }

  getUsername():string {
    let value = this.decodeToken();
    return value['login'];
  }

  loggedIn(): boolean {

    return this.getToken() && !this.isTokenExpired();
  }

  decodeToken(): any {
    return jwtHelper.decodeToken(this.getToken());
  }

  getTokenExpirationDate(): Date {
    if (!this.getToken()) {
      return null;
    }
    return jwtHelper.getTokenExpirationDate(this.getToken());
  }

  isTokenExpired(): boolean {
    return jwtHelper.isTokenExpired(this.getToken());
  }

  getToken(): any {
    return sessionStorage.getItem(JwtService.SESSION_STORAGE_KEYS().TOKEN);
  }

  /**
   * Adds JWT to local storage
   */
  setToken(token): void {
    sessionStorage.setItem(JwtService.SESSION_STORAGE_KEYS().TOKEN, token);
  }

  /**
   * Deletes JWT from local storage
   */
  removeToken(): void {
    sessionStorage.removeItem(JwtService.SESSION_STORAGE_KEYS() .TOKEN);
  }
}
