import { NgModule } from '@angular/core';

import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule, HttpClient }    from '@angular/common/http';
import {TranslateModule, TranslateLoader, MissingTranslationHandler} from '@ngx-translate/core';
import {TranslateHttpLoader} from '@ngx-translate/http-loader';

import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';
import { NgbModule, NgbTypeaheadModule, NgbPaginationModule, NgbDropdownModule, NgbDatepickerModule} from '@ng-bootstrap/ng-bootstrap';
import { ToastrModule } from 'ngx-toastr';

import { FechamentosremComponent } from './fechamentosrem.component';
import { FechamentosremService } from './services/fechamentosrem.service';

import { NgbdSortableHeader } from './directives/sortable.directive';
import { FechamentosremEditComponent } from './editor/fechamentosrem-edit.component';
import { FechamentosremListComponent } from './list/fechamentosrem-list.component';
import { FechamentosremRoutingModule } from './fechamentosrem-routing.module';
import { HttpErrorHandler } from './services/http-error-handler.service';
import { PPWMissingTranslationHandler } from './util/missingTranslationHandler';
import { CommonModule, DatePipe } from '@angular/common';
//import { environment } from 'src/environments/environment';
import { PPWInterceptorProviders } from './interceptors/ppw.interceptor.provider';

import { environment } from '../environments/environment';

//const localeStartingURL = environment.localeUrlFolder;
//const localeFileType = environment.localeFileType;

// AoT requires an exported function for factories
export function HttpLoaderFactory(http: HttpClient) {
  return new TranslateHttpLoader(http, environment.localeUrlFolder, environment.localeFileType);
}

@NgModule({
  declarations: [
     FechamentosremComponent,
     NgbdSortableHeader,
     FechamentosremEditComponent,
     FechamentosremListComponent
  ],
  imports: [
    CommonModule,
    FontAwesomeModule,
    FormsModule,
    ReactiveFormsModule,
    HttpClientModule,
    NgbModule,
    ToastrModule.forRoot() ,
    TranslateModule.forChild({
      missingTranslationHandler: {provide: MissingTranslationHandler, useClass: PPWMissingTranslationHandler},
      loader: { provide: TranslateLoader, useFactory: HttpLoaderFactory, deps: [HttpClient]},
        isolate: true
    }),
    NgbTypeaheadModule,
    NgbPaginationModule,
    NgbDropdownModule,
    NgbDatepickerModule,
    FechamentosremRoutingModule
  ],
  providers:[HttpErrorHandler, PPWInterceptorProviders, FechamentosremService, DatePipe],
  bootstrap: [FechamentosremComponent]
})
export class FechamentosremModule{}
