import { Injectable, Inject, InjectionToken } from '@angular/core';
import {
  HttpRequest,
  HttpHandler,
  HttpEvent,
  HttpInterceptor,
  HttpResponse,
  HttpErrorResponse
} from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { environment } from '../../environments/environment';
import { ActivatedRoute } from '@angular/router';
import { IAppConfig } from '../models/app-config.model';
import { timeout, tap } from 'rxjs/operators';

export const DEFAULT_TIMEOUT = new InjectionToken<number>('defaultTimeout');

@Injectable({
  providedIn: 'root'
})
export class PackageUrlInterceptor implements HttpInterceptor {
  private envConfig: IAppConfig;

  constructor(private route: ActivatedRoute, @Inject(DEFAULT_TIMEOUT) protected defaultTimeout: number) {
    this.envConfig = environment;
  }
  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    const timeoutValue = request.headers.get('timeout') || this.envConfig.timeout || this.defaultTimeout;
    const timeoutValueNumeric = Number(timeoutValue);

    if (request.url.indexOf(environment.localeFileType) === -1 && request.url.indexOf(environment.configFileURL) === -1) {
      let clone = request.clone({
        url: this.envConfig.conppwPadraoEndpoint + request.url
     });
      return next.handle(clone).
        pipe(timeout(timeoutValueNumeric));
    }
    return next.handle(request).pipe(
      timeout(this.envConfig.timeout || this.defaultTimeout));
  }
}
