import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, Router, NavigationEnd, NavigationStart, Event } from '@angular/router';
import { Location } from "@angular/common";
import { FechamentosremService } from './services/fechamentosrem.service';
import { faTimes } from '@fortawesome/free-solid-svg-icons';
import { Observable, Subscription } from 'rxjs';
import { release } from '../environments/release';

@Component({
  selector: 'lib-root',
  templateUrl: './fechamentosrem.component.html',
  styleUrls: ['./fechamentosrem.component.scss'],
})
export class FechamentosremComponent implements OnInit,OnDestroy {

  linkList: string[] = [];
  //private yScrollStack: number[] = [];
  private goBackLink: string;
  empresa:string = "";
  periodo:string = "";
  faTimes = faTimes;
  
  showLoading: boolean = false;
  arqLoading$: Observable<boolean>;

  loadSubs:Subscription;
  release: string = release.version;

  constructor(private route: ActivatedRoute,
              private router: Router,
              //private location: Location,
              private fechamentosremService: FechamentosremService) {
      this.arqLoading$ = this.fechamentosremService.loading$;
      this.loadSubs = this.arqLoading$.subscribe((status)=>{
        this.showLoading = status;
      });
      this.goBackLink = '/external-'+ this.fechamentosremService.getResourceName();
  }

  ngOnInit() {
    this.route.params.subscribe(params => {
      console.log(params)
      if (params.hasOwnProperty("empresa"))
        this.empresa = params.get("empresa");
      
      if (params.hasOwnProperty("periodo"))
        this.periodo = params.get("periodo");
    });
    /*this.router.events.subscribe((evt) => {
      if (!(evt instanceof NavigationEnd)) {
          return;
      }
      window.scrollTo(0, 0)
    });*/

    /*const scrollLevels: { [navigationId: number]: number } = {};
    let lastId = 0;
    let restoredId: number;

    this.router.events.subscribe((ev: Event) => {

      if (ev instanceof NavigationStart) {
        scrollLevels[lastId] = window.scrollY;
        lastId = ev.id;
        this.linkList.push(ev.url);
        restoredId = ev.restoredState ? ev.restoredState.navigationId : undefined;
      }

      if (ev instanceof NavigationEnd) {
        if (restoredId) {
          // Optional: Wrap a timeout around the next line to wait for
          // the component to finish loading
          
          this.goBackLink = this.linkList.pop();
          window.scrollTo(0, scrollLevels[restoredId] || 0);
        } else {
          window.scrollTo(0, 0);
        }
      }

    });*/

  }

  goback(){
    this.router.navigate([this.goBackLink]);
  }

  ngOnDestroy() {
    console.log('Cleaning Fechamentos...');
    if (this.loadSubs != undefined) this.loadSubs.unsubscribe();
  }
  
}
