import { Injectable } from '@angular/core';
import {
  HttpRequest,
  HttpHandler,
  HttpEvent,
  HttpInterceptor,
  HttpResponse,
  HttpErrorResponse
} from '@angular/common/http';

import { Observable } from 'rxjs/Observable';
import { HttpErrorHandler, HandleError } from '../services/http-error-handler.service';
import { environment } from '../../environments/environment';
import { tap, catchError } from 'rxjs/operators';
import { ActivatedRoute } from '@angular/router';
import { IAppConfig } from '../models/app-config.model';

@Injectable({
  providedIn: 'root'
})
export class JwtInterceptor implements HttpInterceptor {

  static SESSION_STORAGE_TOKEN: string = 'auth-token';
  private handleError: HandleError;
  private envConfig: IAppConfig;

  constructor(
    private route: ActivatedRoute,
    private httpErrorHandler: HttpErrorHandler
    ) {
    this.envConfig = <IAppConfig>this.route.snapshot.data;
    this.handleError = httpErrorHandler.createHandleError(environment.projectName);
  }
  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {

    request = request.clone({
      setHeaders: {
        Authorization: `Bearer ${sessionStorage.getItem(JwtInterceptor.SESSION_STORAGE_TOKEN)}`
      }
    });
    //return this.config.load().pipe( take(1), switchMap( (configData) =>
    return next.handle(request).
      pipe(
        tap((event: HttpEvent<any>) => {
          if (event instanceof HttpResponse) {
            console.log(`${environment.projectName}: request realizado=${event.status}/${event.statusText}`)
          }
        }),
        catchError(this.handleError<HttpEvent<any>>('Erro'))
      )
    //));
  }
}
