import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { FechamentosremComponent } from './fechamentosrem.component';
import { FechamentosremListComponent } from './list/fechamentosrem-list.component';
import { FechamentosremEditComponent } from './editor/fechamentosrem-edit.component';
import { ProjectConfigResolver } from './interceptors/project.config.resolver';

@NgModule({
  imports: [
      RouterModule.forChild([
        {path: '', component: FechamentosremComponent,
          children: [
            {path: '', component: FechamentosremListComponent},
            {path: ':empresa',component: FechamentosremEditComponent},
            {path: ':empresa/:periodo',component: FechamentosremEditComponent }
          ],resolve: {
            envConfiguration: ProjectConfigResolver
          }}
    ])
  ],
  exports: [RouterModule]
})
export class FechamentosremRoutingModule { }
