const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

function compare(v1, v2) {
  return v1 < v2 ? -1 : v1 > v2 ? 1 : 0;
}

function sort(items: Remuneracao[], column: string, direction: string): Remuneracao[] {
  if (direction === '') {
    return items;
  } else {
    return [...items].sort((a, b) => {
      const res = compare(a[column], b[column]);
      return direction === 'asc' ? res : -res;
    });
  }
}

function matches(object: Remuneracao, text: string, text2: string) {
  const term = text.toLowerCase();
  const periodo = text2.toLowerCase();
  return (object.idEmpresa.toLowerCase().includes(term) || object.nomeEmpresa.toLowerCase().includes(term))
    && object.dataRecebimento.toLowerCase().includes(periodo);
}

function fullMatch(criterio: any, idEmpresa: string, periodo: string) {
  let item: Remuneracao = criterio as Remuneracao;
  return (periodo === null) ?
    (item.idEmpresa === idEmpresa) :
    (item.idEmpresa === idEmpresa && item.dataRecebimento === periodo);
}

@Injectable({
  providedIn: 'root'
})
export class FechamentosremService {
  private pdfUrl: string = '/CreatePDFServlet';
  //private serviceUrl = '/conppwPadraoC9-war/ppwpadrao/v9/remuneracoes';
  private _serviceName = "Fechamentosrem";
  private handleError: HandleError;

  private listaTerceirizadas: Assessoria[] = [];
  private listaPrevias: Remuneracao[] = [];
  private _loading$ = new BehaviorSubject<boolean>(false);
  private _search$ = new Subject<void>();
  private _terceirizadas$ = new BehaviorSubject<Assessoria[]>([]);
  private _previas$ = new BehaviorSubject<Remuneracao[]>([]);
  private _total$ = new BehaviorSubject<number>(0);

  private originalCopy: Remuneracao = null;

  private _state: any = {
    page: 1,
    pageSize: 10,
    assessoriaSearchTerm: '',
    periodoSearchTerm: '',
    sortColumn: '',
    sortDirection: '',
  };

  constructor(private http: HttpClient,
    private httpErrorHandler: HttpErrorHandler,
    private toastr: ToastrService) {
    this.handleError = httpErrorHandler.createHandleError(this._serviceName);
    /*this.load([]).subscribe(items => {
      this._loading$.next(false);
      this.listaPrevias = items;
      this._previas$.next(items);
      this._search$.next();
    });*/
    this._search$.pipe(
      tap(() => this._loading$.next(true)),
      debounceTime(200),
      switchMap(() => this._search()),
      delay(200),
      tap(() => this._loading$.next(false))
    ).subscribe(result => {
      this._previas$.next(result.items);
      this._total$.next(result.total);
    });
    this.loadTerceirizadas();
  }

  get previas$() { return this._previas$.asObservable(); }
  get total$() { return this._total$.asObservable(); }
  get loading$() { return this._loading$.asObservable(); }
  get page() { return this._state.page; }
  get pageSize() { return this._state.pageSize; }
  get assessoriaSearchTerm() { return this._state.assessoriaSearchTerm; }
  get periodoSearchTerm() { return this._state.periodoSearchTerm; }

  set page(page: number) { this._set({ page }); }
  set pageSize(pageSize: number) { this._set({ pageSize }); }
  set assessoriaSearchTerm(assessoriaSearchTerm: string) { this._set({ assessoriaSearchTerm }); }
  set periodoSearchTerm(periodoSearchTerm: string) { this._set({ periodoSearchTerm }); }
  set sortColumn(sortColumn: string) { this._set({ sortColumn }); }
  set sortDirection(sortDirection: SortDirection) { this._set({ sortDirection }); }

  private _set(patch: Partial<any>) {
    Object.assign(this._state, patch);
    this._search$.next();
  }

  private _search(): Observable<SearchResult<Remuneracao>> {
    const { sortColumn, sortDirection, pageSize, page, assessoriaSearchTerm, periodoSearchTerm } = this._state;

    // 1. sort
    let items = sort(this.listaPrevias, sortColumn, sortDirection);

    if (assessoriaSearchTerm.length < 2 && periodoSearchTerm.length < 2) {
      const total = items.length;
      return of({ items, total });
    }

    // 2. filter
    items = items.filter(item => matches(item, assessoriaSearchTerm, periodoSearchTerm));
    const total = items.length;

    // 3. paginate
    items = items.slice((page - 1) * pageSize, (page - 1) * pageSize + pageSize);

    //this.messageService.add("success", this._serviceName + ": Lista de items selecionados");

    return of({ items, total });

  }

  getResourceName(): string {
    return 'fechamentosrem';
  }
  /*private log(message: string) {
     this.messageService.add("info",` ${this._serviceName}: ${message}`);
     console.log(message);
  }*/

  selectItem(idEmpresa: string, periodo: string): Observable<Remuneracao> {
    if (idEmpresa === null || idEmpresa === undefined) {
      console.log(`${this._serviceName}: id da empresa selecionada Ã© invÃ¡lida!`);
      let msg = `id da empresa escolhida Ã© invÃ¡lida!`;
      this.toastr.error(msg, "CyberMessenger");
      return of(null);
    }
    let selectedItem: Remuneracao = this.listaPrevias.find(item => fullMatch(item, idEmpresa, periodo));
    this.originalCopy = JSON.parse(JSON.stringify(selectedItem));
    //this._selectedItem$.next(this.selectedItem);
    return of(selectedItem);
  }

  loadTerceirizadas(): void {
    //this.messageService.add("info", this._serviceName + " Lista de items selecionados");
    console.log(this._serviceName + " Lista de items selecionados");
    this.getTerceirizadas().subscribe(terceirizadas => {
      this.listaTerceirizadas = terceirizadas;
      this._terceirizadas$.next(this.listaTerceirizadas);
    });
  }

  getTerceirizadas(): Observable<Assessoria[]> {
    return this.http.get<Assessoria[]>(`${environment.serviceUrls.serviceUrl}/terceirizadas`)
      .pipe(
        tap(items => {
          console.log(`${this._serviceName}: Assessorias selecionadas [${items.length}]`)
        }),
        catchError(this.handleError<Assessoria[]>('getTerceirizadas', []))
      );
  }
  /*loadTerceirizadas(): Observable<Assessoria[]> {
    //this.messageService.add("info", this._serviceName + " Lista de items selecionados");
    console.log(this._serviceName + " Lista de items selecionados");
    return this.http.get<Assessoria[]>(this.serviceUrl + "/terceirizadas")
      .pipe(tap(_ => console.log('fetched items')),
        catchError(this.handleError('getTerceirizadas', []))
      );

  }*/

  //load(idEmpresa?: string, periodo?: string, codContrato?: string): Observable<Remuneracao[]> {
  //load(): Observable<Remuneracao[]> {


  load(params: Array<string>): void {
    this.getItems(params)
      .subscribe(items => {
        this.listaPrevias = items;
        this._previas$.next(this.listaPrevias);
        this._search$.next();
      });
  }

  getItems(params: Array<string>): Observable<Remuneracao[]> {
    this._loading$.next(true);
    let urlCall: string = environment.serviceUrls.serviceUrl;
    params.forEach((paramItem: string) => urlCall += (typeof paramItem !== 'undefined' ? `/${paramItem}` : ""));
    //urlCall = urlCall + (typeof idEmpresa !== 'undefined' ? `/${idEmpresa}` : "");
    //urlCall = urlCall + (typeof periodo !== 'undefined' ? `/${periodo}` : "");
    //urlCall = urlCall + (typeof codContrato !== 'undefined' ? `/${codContrato}` : "");

    return this.http.get<Remuneracao[]>(urlCall)
      .pipe(
        //tap(data => this.log(`${this._serviceName}/load`, data)),
        finalize(() => this._loading$.next(false)),
        catchError(this.handleError<Remuneracao[]>('Carregar Fechamentos', []))
      );
  }

  fechar(params: Array<string> = null, user: string, done) {
    let httpOpts = {
      headers: new HttpHeaders({ 'Content-Type': 'application/json', 'Usuario': user })
    };

    if (params === null || params.length < 1 || params.every((paramItem: string) => !paramItem.trim())) {
      return of("");
    }

    let urlCall: string = environment.serviceUrls.serviceUrl;
    params.forEach((paramItem: string) => urlCall += (typeof paramItem !== 'undefined' ? `/${paramItem}` : ""));

    this._loading$.next(true);
    let retVal: Observable<string> =
      this.http.put<string>(urlCall, httpOpts);
    retVal.pipe(
      finalize(() => this._loading$.next(false)),
      tap(_ => console.log(_)),
      catchError(this.handleError<string>('fechamentoRemuneracao', ""))
    ).subscribe(response => {
      let msg = `Fechamento de RemuneraÃ§Ã£o realizado com sucesso!`;
      this.toastr.success(msg, "CyberMessenger");
      done();
    }, error => {
      console.log("Ocorreu uma falha ao fechar remuneracao!: " + error.error.message);
      this.toastr.error("Ocorreu uma falha ao fechar remuneracao!", "CyberMessenger");
    });
  }

  glosa(idEmpresa: string, periodo: string, numContrato: string): Observable<string | Object> {
    if (idEmpresa.trim() || periodo.trim()) {
      return of("");
    }
    //const url = `${this.httpUrl}/${environment.serviceUrls.serviceUrl}/?term=${term}`;
    const url = `${environment.serviceUrls.serviceUrl}/glosa`;
    return this.http.post(`${environment.serviceUrls.serviceUrl}`, { "idEmpresaTerceirizada": idEmpresa, "periodo": periodo, "numContrato": numContrato }, httpOptions).pipe(
      tap(_ => console.log(_)),
      catchError(this.handleError<string>('fechamentoRemuneracao', ""))
    );
  }

  include(item: Remuneracao, keyName: string): Observable<any> {
    return this.http.put(`${environment.serviceUrls.serviceUrl}`, item, httpOptions).pipe(
      tap(
        data => {
          if (data['entity'] === null) {
            throwError(`Ocorreu um erro durante a incluisÃ£o do item cÃ³digo=${item[keyName]}}.`);
          } else {
            console.log(`${this._serviceName}: Item cÃ³digo=${data['entity'][keyName]} incluido com sucesso!`);
            let msg = `CritÃ©rio ${data['entity'][keyName]} salvo com sucesso!`;
            this.toastr.success(msg, "CyberMessenger");

            this.listaPrevias.unshift(data['entity']);
            this._previas$.next(this.listaPrevias);
          }
        }
      ),
      catchError(this.handleError<Remuneracao>('Incluir'))
    );
  }

  save(items: Remuneracao[]): Observable<Remuneracao[]> {
    return this.http.post(environment.serviceUrls.serviceUrl, items, httpOptions).pipe(
      tap(
        (changedList: any) => {
          if (changedList['entity'] == null) {
            throwError('Ocorreu um erro durante a atualizaÃ§Ã£o do item.');
          } else {
            console.log(`${this._serviceName}: Foram atualizados ${items.length} items com sucesso!`);
            let msg = `Item alterado com sucesso!`;
            this.toastr.success(msg, "CyberMessenger");
            this._previas$.next(this.listaPrevias);
            this._search$.next();
          }
        }
      ),
      catchError(this.handleError<Remuneracao[]>('Salvar'))
    );
  }

  /** DELETE: delete item from the server */
  remove(item: Remuneracao | number, keyName?: string): Observable<Remuneracao> {
    const id = typeof item === 'number' ? item : item[keyName];
    return this.http.delete<Remuneracao>(`${environment.serviceUrls.serviceUrl}/${id}`, httpOptions).pipe(
      tap(
        _ => {
          let itemIndex = this.listaPrevias.findIndex((item) => item['id'] === id);
          if (itemIndex >= 0) {
            this.listaPrevias.splice(itemIndex, 1);
            console.log(`Item [${id}] removido com sucesso`);
          }
          let msg = `Item removido com sucesso!`;
          this.toastr.success(msg, "CyberMessenger");
          this._previas$.next(this.listaPrevias);
        }
      ),
      catchError(this.handleError<Remuneracao>('Remover'))
    );
  }

  filter(term: string, periodo: string): void {
    if (!term.trim()) {
      this.load([]);
      return;
    }
    //const url = `${this.httpUrl}/${environment.serviceUrls.serviceUrl}/?term=${term}`;
    const url = `${environment.serviceUrls.serviceUrl}/${term}/${periodo}`;
    this.http.get<Remuneracao[]>(url).pipe(
      tap(_ => console.log(_)),
      catchError(this.handleError<Remuneracao[]>('Filtrar', []))
    ).subscribe(items => {
      this.listaPrevias = items;
      this._previas$.next(this.listaPrevias);
      this._search$.next();
    });
    //tap(_ =>console.log(`found items matching "${term}"`)),
  }

  /**
   * Handle Http operation that succeded.
   * @param operation - name of the operation that succeded
   * @param result - Result value to return as the observable
   */
  private log<T>(operation: string, result: T) {
    // TODO: better job of transforming error for user consumption
    console.log(`${operation} succeded`);

    // Let the app keep running by returning an empty result.
    //return of(result as T);
    return result;
  }

  /**
   * Handle Http operation that failed.
   * Let the app continue.
   * @param operation - name of the operation that failed
   * @param result - optional value to return as the observable result
   */
}
