import { Component, Input, OnDestroy, OnInit, QueryList, ViewChildren } from '@angular/core';
import { DecimalPipe } from '@angular/common';
import { ToastrService } from 'ngx-toastr';
import { TranslateService } from '@ngx-translate/core';
import { Observable, Subscription } from 'rxjs';
import { FechamentosremService } from '../services/fechamentosrem.service';
import { PDFService } from '../services/pdf.service';
import { Remuneracao } from '../models/remuneracao.model';

import { NgbdSortableHeader, SortEvent } from '../directives/sortable.directive';
import { faPrint, faTrashAlt, faKey, faSearch, faCalendarAlt, faSort } from '@fortawesome/free-solid-svg-icons';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'ppw-fechamentosrem-list',
  templateUrl: './fechamentosrem-list.component.html',
  styleUrls: ['../fechamentosrem.component.scss'],
  providers: [FechamentosremService, PDFService, ToastrService, DecimalPipe]
})
export class FechamentosremListComponent implements OnInit,OnDestroy {
  title = '';
  resourceName = '/';
  faPrint = faPrint;
  faKey = faKey;
  faTrashAlt = faTrashAlt;
  faSearch = faSearch;
  faCalendar = faCalendarAlt;
  faSort = faSort;

  private _valorFechamento: number = 0;
  private _empresaCobranca: string = "";
  private _periodo: string = "";
  msgResult: string;

  loading$: Observable<boolean>;
  remuneracoes$: Observable<Remuneracao[]>;
  total$: Observable<number>;

  remuneracoesSub: Subscription = null;

  selectedId: string = "";
  static LOCALE_KEY = 'currentLocale';

  @ViewChildren(NgbdSortableHeader) headers: QueryList<NgbdSortableHeader>;
  constructor(private route: ActivatedRoute,
    private router: Router,
    private requestHandlerService: FechamentosremService,
    private pdfHandlerService: PDFService,
    private toastr: ToastrService,
    public translate: TranslateService) {

    translate.setDefaultLang('ptBR');
    const currentLocale = sessionStorage.getItem(FechamentosremListComponent.LOCALE_KEY);

    translate.setDefaultLang(currentLocale);
    this.resourceName = '/external-' + requestHandlerService.getResourceName();
    this.loading$ = requestHandlerService.loading$;
    this.total$ = requestHandlerService.total$;
    this.remuneracoes$ = requestHandlerService.previas$;
  }

  get page() { return this.requestHandlerService.page; }
  get pageSize() { return this.requestHandlerService.pageSize; }
  get periodoSearchTerm() { return this.requestHandlerService.periodoSearchTerm; }
  get assessoriaSearchTerm() { return this.requestHandlerService.assessoriaSearchTerm; }

  set page(page: number) { this.requestHandlerService.page = page; }
  set pageSize(pageSize: number) { this.requestHandlerService.pageSize = pageSize; }
  set periodoSearchTerm(searchTerm: string) { this.requestHandlerService.periodoSearchTerm = searchTerm; }
  set assessoriaSearchTerm(searchTerm: string) { this.requestHandlerService.assessoriaSearchTerm = searchTerm; }


  onSort({ column, direction }: SortEvent) {
    // resetting other headers
    this.headers.forEach(header => {
      if (header.sortable !== column) {
        header.direction = '';
      }
    });

    // sorting items
    this.requestHandlerService.sortColumn = column;
    this.requestHandlerService.sortDirection = direction;
  }

  ngOnInit() {
    this.remuneracoesSub = this.remuneracoes$.subscribe(values => {
      this.pdfHandlerService.previas = (values!=undefined)?values:[];
      this.pdfHandlerService.total = (values!=undefined && values!=null)?values.length:0;
    });
    this.refreshList();
  }
  ngOnDestroy() {
    console.log('Cleaning Subs...');
    if (this.remuneracoesSub != undefined) {
      this.remuneracoesSub.unsubscribe();
      this.remuneracoesSub.remove(this.remuneracoesSub);
    }
  }
  clear(): void {
    this.assessoriaSearchTerm = "";
    this.periodoSearchTerm = "";
    this.headers.forEach(header => {
      header.direction = '';
    });
    this.refreshList();
  }

  refreshList(){
    this.requestHandlerService.load([]);
  }
  
  fechar(remuneracao: Remuneracao): void {
    this._valorFechamento = remuneracao.remuneracaoTotalView;
    this._empresaCobranca = remuneracao.idEmpresa;
    this._periodo = remuneracao.dataRecebimento.replace("/", "");
    this.router.navigate([this.resourceName, remuneracao.idEmpresa, this._periodo]);
  }

  emitirRelatorioPDF() {
    this.pdfHandlerService.assessoria = this.periodoSearchTerm;
    this.pdfHandlerService.periodo = this._empresaCobranca;
    this.pdfHandlerService.requestPdf();
  }
  

  /*selecionaFechamento(remuneracao:Remuneracao):void {
    this._valorFechamento = remuneracao.remuneracaoTotalView;
    this._empresaCobranca = remuneracao.idEmpresa;
    this._periodo = remuneracao.dataRecebimento.replace("/","");
    const modalRef = this.modalService.open(NgbdModalContent);
    modalRef.componentInstance.content = remuneracao;
    modalRef.componentInstance.periodo = this._periodo;
    modalRef.result.then((result) => {
        if(result.success)
          this.toastr.success(result.message, "CyberMessenger")
        else this.toastr.error(result.message, "CyberMessenger");
        //console.log(result);
     }, (reason) => {
        this.msgResult = `Dismissed ${this.getDismissReason(reason)}`;
     });
  }*/
}