import { Injectable } from '@angular/core';


import { DecimalPipe, DatePipe } from '@angular/common';
import { Observable, of } from 'rxjs';

import { Remuneracao } from '../models/remuneracao.model';

//import * as jsPDF from 'jspdf';

declare var jsPDF: any;

@Injectable({
  providedIn: 'root'
})
export class PDFService {

  private listaPrevias: Remuneracao[] = [];

  private _pdfPath: string = 'FechamentoRemuneracao.pdf';
  private _pdfConfig: any = {
    orientation: 'landscape',
    unit: 'mm',
    format: 'a4'
  };
  private _pdfValues: any = {
    total: 0,
    previas: [],
    filtros: [],
    assessoria: '',
    periodo: ''
  };


  constructor(private decimalPipe: DecimalPipe,
              private datePipe: DatePipe) { }

  set orientation(page: number) { this._setConfig({ page }); }
  set unit(pageSize: number) { this._setConfig({ pageSize }); }
  set format(nomeSearchTerm: string) { this._setConfig({ nomeSearchTerm }); }

  set total(total: number) { this._setValue({ total }); }
  set previas(previas: Remuneracao[]) { this._setValue({ previas }); }
  set filtros(filtros: string[]) { this._setValue({ filtros }); }
  set assessoria(assessoria: string) { this._setValue({ assessoria }); }
  set periodo(periodo: string) { this._setValue({ periodo }); }
  set nomeArquivo(nomeArquivo: string) { this._pdfPath = nomeArquivo; }

  private _setConfig(patch: Partial<any>) {
    Object.assign(this._pdfConfig, patch);
  }

  private _setValue(patch: Partial<any>) {
    Object.assign(this._pdfValues, patch);
  }

  requestPdf() {
      if (this._pdfValues.previas.length < 1) {
          return false;
      }
      else {
          if (this._pdfValues.filtros.length < 1) {
              var filtros = ["Sem filtros"];
              this._setValue({ filtros });
          }
          this.generatePdf();
          return true;
      }
  };

  private generatePdf() {
    let linhaTxt = "";
    let linha = 36;//Controle de linhas
    let j = 0;
    let pagina = 0;//Controle de Numeracao Paginas
    let vlTotalRecuperado = 0;
    let vlTotalRemuneracao = 0;
    let vlTotalEstornos = 0;
    let vlTotalGlosas = 0;
    let vlTotalRemGeral = 0;
    let qtdTotalRemGeral = 0;
    let qtdTotalRem = 0;
    let filtros = 0;

    let doc = new jsPDF(this._pdfConfig);
    doc.setDisplayMode('fullwidth');
    //doc.setMargins(5, 5, 5, 5);
    doc.setFont("courier", "normal");
    doc.setFontSize(10);
	  doc.setLineWidth(0.25);

    let lineWidth = doc.internal.pageSize.getWidth()-10;

    for (let i = 0; i < this._pdfValues.previas.length; i++) {

      var remuneracao = this._pdfValues.previas[i];
      //Controle de cabeÃ§alho, linhas e pagina
      if (j == 0) {
        pagina++;
        if (pagina > 1)
          doc.addPage();
        doc.setFont("courier", "normal");
        //CabeÃ§alho
        linha = 12;

        var now = new Date();
        doc.text(this.datePipe.transform(now, 'dd/MM/yyyy') + "                                 RELATORIO FECHAMENTO DE REMUNERAÃÃO                                         PAG -" + pagina.toString().padStart(7), 10, linha);
        linha = linha + 4; //"                                                                                                              "
        doc.text(this.datePipe.transform(now, 'HH:mm:ss') + "                                                                                                              ", 10, linha);
        linha = linha + 4;
        //doc.text("                                           RELATORIO FECHAMENTO DE REMUNERAÃÃO                                   ",1,linha);//+objRenegociacaoAtual.dtMovimento,1,20);
        //linha = linha + 4;
        doc.line(10, linha, lineWidth, linha, "FD");
        //doc.text(tracejado, 10, linha);
        j = 39;
        if (filtros == 0) {
          filtros = 1;
          linha = linha + 4;
          doc.text("FILTROS UTILIZADOS", 10, linha);
          for (let x = 0; x < this._pdfValues.filtros.length; x++) {
            linha = linha + 4;
            doc.text(this._pdfValues.filtros[x], 10, linha);
            j--;
          }
          linha = linha + 4;
          //doc.text(tracejado, 10, linha);
          j--;
        }
        doc.setLineWidth(0.5);
		    doc.rect(10, linha, lineWidth-10, 12,  "S");
        doc.setLineWidth(0.25);
        linha = linha + 4;
        doc.text(" ASSESSORIA                                 QTD  PAGTOS        VALOR               REMUNERAÃÃO          PERÃODO", 10, linha);
        linha = linha + 4;
        doc.text(" COBRANÃA                                   RECUPERADOS        RECUPERADO          TOTAL                    ", 10, linha);
        linha = linha + 4;
        //doc.text(tracejado, 10, linha);
        linha = linha + 4;
      }
      pagina.toString().padStart(7);
      linhaTxt = "(" + remuneracao.idEmpresa.padEnd(7) + ")" + remuneracao.nomeEmpresa.padEnd(34);
      linhaTxt = "" + linhaTxt + this.decimalPipe.transform(remuneracao.qtdPagto, '1.2-2').toString().padStart(11);
      linhaTxt = "" + linhaTxt + this.decimalPipe.transform(remuneracao.valorRecuperadoView, '1.2-2').toString().padStart(18);
      linhaTxt = "" + linhaTxt + this.decimalPipe.transform(remuneracao.remuneracaoTotalView, '1.2-2').toString().padStart(21);
      linhaTxt = "" + linhaTxt + remuneracao.dataRecebimento.padStart(17);
      doc.text(linhaTxt, 10, linha);
      vlTotalRecuperado = vlTotalRecuperado + remuneracao.valorRecuperadoView;
      vlTotalRemuneracao = vlTotalRemuneracao + remuneracao.valorRemuneracaoView;
      vlTotalEstornos = vlTotalEstornos + remuneracao.valorEstornosView;
      vlTotalGlosas = vlTotalGlosas + remuneracao.valorGlosasView;
      vlTotalRemGeral = vlTotalRemGeral + remuneracao.remuneracaoTotalView;
      qtdTotalRem = qtdTotalRem + remuneracao.qtdPagto;
      qtdTotalRemGeral = qtdTotalRemGeral + remuneracao.qtdPagto;
      //linha final pagina
      if (j == 2) {
        linha = linha + 4;
        doc.line(10, linha, lineWidth, linha, "FD");
        //doc.text(tracejado, 10, linha);
        j--;
      }
      linha = linha + 4;
      j--;
    }
    //Verifica se esta terminando as linhas das paginas e insere um novo cabeÃ§alho
    if (j <= 4) {
      doc.line(10, linha, lineWidth, linha, "FD");
      //doc.text(tracejado, 10, linha);
      pagina++;
      doc.addPage();
      doc.setFont("courier", "normal");
      //CabeÃ§alho
      linha = 12; //"                 RELATORIO FECHAMENTO DE REMUNERAÃÃO                                                         "
      //"                 RELATORIO FECHAMENTO DE REMUNERAÃÃO                                                         "

      var now = new Date();
      doc.text(this.datePipe.transform(now, 'dd/MM/yyyy') + "                 RELATORIO FECHAMENTO DE REMUNERAÃÃO                                                         PAG -" + pagina.toString().padStart(7), 10, linha);
      linha = linha + 4; //"                                                                                                              "
      doc.text(this.datePipe.transform(now, 'HH:mm:ss') + "                                                                                                              ", 10, linha);
      linha = linha + 4;
      doc.line(10, linha, lineWidth, linha, "FD");
      //doc.text(tracejado, 10, linha);
      j = 39;
      if (filtros == 0) {
        filtros = 1;
        linha = linha + 4;
        doc.text("FILTROS UTILIZADOS", 10, linha);
        for (let x = 0; x < this._pdfValues.filtros.length; x++) {
          linha = linha + 4;
          doc.text(this._pdfValues.filtros[x], 10, linha);
          j--;
        }
        linha = linha + 4;
        //doc.text(tracejado, 10, linha);
        j--;
      }
      doc.setLineWidth(0.5);
      doc.rect(10, linha, lineWidth-10, 12,  "S");
      doc.setLineWidth(0.25);
      linha = linha + 4;
      doc.text(" ASSESSORIA                                 QTD  PAGTOS        VALOR               REMUNERAÃÃO          PERÃODO", 10, linha);
      linha = linha + 4;
      doc.text(" COBRANÃA                                   RECUPERADOS        RECUPERADO          TOTAL                 ", 10, linha);
      linha = linha + 4;
      doc.line(10, linha, lineWidth, linha, "FD");
      linha = linha + 4;
      linha = 36;
      doc.line(10, linha, lineWidth, linha, "FD");
      linha = linha + 4;
      linhaTxt = "Totais:";
      linhaTxt = linhaTxt + qtdTotalRemGeral.toString().padStart(48);
      linhaTxt = linhaTxt + this.decimalPipe.transform(vlTotalRecuperado, '1.2-2').toString().padStart(18);
      linhaTxt = linhaTxt + this.decimalPipe.transform(vlTotalRemGeral, '1.2-2').toString().padStart(21);
      doc.text(linhaTxt, 10, linha);
    }
    else {
      //Verifica se foi inserida a linha final da pagina
      doc.setLineWidth(0.5);
      doc.line(10, linha, lineWidth, linha, "FD");
      linha = linha + 4;
      linhaTxt = "Totais:";
      linhaTxt = linhaTxt + qtdTotalRemGeral.toString().padStart(48);
      linhaTxt = linhaTxt + this.decimalPipe.transform(vlTotalRecuperado, '1.2-2').toString().padStart(18);
      linhaTxt = linhaTxt + this.decimalPipe.transform(vlTotalRemGeral, '1.2-2').toString().padStart(21);
      doc.text(linhaTxt, 10, linha);
    }
    //doc.save( Method.REMOTE, serveletPDF, "FechamentoRemuneracao.pdf"  );
    //doc.output("dataurlnewwindow", "FechamentoRemuneracao.pdf");
    doc.save(this._pdfPath);
    //doc.save('FechamentoRemuneracao.pdf');
  }


	/**
	 * Handle Http operation that failed.
	 * Let the app continue.
	 * @param operation - name of the operation that failed
	 * @param result - optional value to return as the observable result
	 */
  private handleError<Remuneracao>(operation = 'operation', result?: Remuneracao) {
    return (error: any): Observable<Remuneracao> => {

      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead

      // TODO: better job of transforming error for user consumption
      console.log(`${operation} failed: ${error.message}`);

      // Let the app keep running by returning an empty result.
      return of(result as Remuneracao);
    };
  }
}
